<template>
  <div>
    <v-row v-if="statusSuccess" class="px-4" justify="center">
      <v-col cols="12" class="text-center mt-5">
        <div class="title">
          {{ thankYouForYourRequest }}
           <v-icon class="theme-bg-color" large>mdi-checkbox-marked-circle-outline</v-icon>
        </div>
      </v-col>
      <v-col cols="12" class="text-center my-2">
        <div class="subheading">
          {{ requestHasBeenSentSubText }}
        </div>
      </v-col>
      <v-col cols="12" :key="data.label" v-for="(data, i) in summaryData" :class="{ 'light-bg' : [i] % 2 === 0, 'dark-bg' : [i] % 2 !== 0 }">
        <div class="submit-data-container">
          <span>
            {{ data.label }}
          </span>
          <span class="bold-text">
            {{ data.value }}
          </span>
        </div>
      </v-col>
      <v-col class="text-center mt-5" cols="12">
        <v-btn depressed class="theme-bg mt-5" @click="clearAll(true)">
          {{ clear }}
          <v-icon class="theme-color pl-3">mdi-reload</v-icon>
        </v-btn>
      </v-col>
    </v-row>

    <v-row v-if="!statusSuccess" class="px-4" justify="center">
      <v-col cols="12" class="text-center mt-5">
        <div class="title">
          {{ requestCouldNotBeProcessed }}
        </div>
      </v-col>
      <v-col cols="12" class="text-center my-2">
        <div class="subheading">
          {{ requestCouldNotBeProcessedSubText }}
        </div>
      </v-col>
      <v-col cols="12" :key="data.label" v-for="(data, i) in summaryData" :class="{ 'light-bg' : [i] % 2 === 0, 'dark-bg' : [i] % 2 !== 0 }">
        <div class="submit-data-container">
          <span>
            {{ data.label }}
          </span>
          <span class="bold-text">
            {{ data.value }}
          </span>
        </div>
      </v-col>
      <v-col class="text-center mt-5" cols="12">
        <v-btn depressed class="theme-bg mt-5" @click="clearAll(false)">
          Try again
          <v-icon class="theme-color pl-3">mdi-reload</v-icon>
        </v-btn>
      </v-col>
    </v-row>

  </div>
</template>

<script>
import { statusTypes } from '@/utils/types'
import { mapState } from 'vuex'

export default {
  name: 'Status-body',
  props: {
    status: { type: String, required: true }
  },
  computed: {
    ...mapState({
      config: ({ appState }) => appState.config,
      formData: ({ userData }) => userData.formData,
      textKeys: ({ appState }) => appState.textKeys
    }),
    summaryData () {
      return [
        {
          label: 'Subject:',
          value: this.formData.subject
        },
        {
          label: 'Your email:',
          value: this.formData.email
        },
        {
          label: 'Your name:',
          value: this.formData.userName
        },
        {
          label: 'Message:',
          value: this.shortenMessage(this.formData.message)
        }
      ]
    },
    statusSuccess () {
      return this.status === statusTypes.SUCCESS
    },
    thankYouForYourRequest () {
      return this.textKeys.thankYouForYourRequest || '{thankYouForYourRequest}'
    },
    requestHasBeenSentSubText () {
      return this.textKeys.requestHasBeenSentSubText || '{requestHasBeenSentSubText}'
    },
    requestCouldNotBeProcessed () {
      const msg = 'Opps.. we could not process your request!'
      return this.textKeys.requestCouldNotBeProcessed || msg
    },
    requestCouldNotBeProcessedSubText () {
      const msg = `We could not process your request. Please try again or contact support directly at ${this.config.supportEmail}`
      return this.textKeys.requestCouldNotBeProcessedSubText || msg
    },
    clear () {
      return this.textKeys.clear || '{clear}'
    }
  },
  methods: {
    shortenMessage (msg) {
      return msg.substr(0, 50) + '...'
    },
    clearAll (reloadPage) {
      this.$router.push({ path: '/', query: this.$route.query })
        .then(() => {
          reloadPage && window.location.reload() // Clear all data
        })
    }
  }
}
</script>

<style lang="scss" scoped>
.submit-data-container {
  display: flex;
  justify-content: space-between;
}
.light-bg {
  background: #ffffff;
}
.dark-bg {
  background: #efefef;
}
</style>
